.gallery-container {
  background-color: #f6f6f6;
  border-radius: 25px;
  margin: 45px 0 50px;
  display: flex;
  flex-wrap: wrap;
  padding: 50px 50px;
  gap: 50px;
}

.gallery-item {
  flex: 1 1 30%;
  position: relative;
  text-decoration: none;
  transition: all 0.4s ease-in-out;
}
.gallery-item img {
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: cover;
  border-radius: 10px;
}
.gallery-item h3 {
  position: absolute;
  top: calc(90% - 20px);
  left: 5%;
  width: 90%;
  color: #ffffff;
  font-size: 18px;
  font-weight: 500;
}

.gallery-item::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 4px);
  background: linear-gradient(rgba(255, 255, 255, 0), #000000);
  opacity: 0.6;
  border-radius: 25px;
  object-fit: cover;
  border-radius: 10px;
}

.gallery-item:hover {
  transform: scale(1.1);
}

.gallery-item:hover:before {
  opacity: 0.2;
}

.gallery-item:hover > :last-child {
  display: none;
}

.hide {
  visibility: hidden;
}

@media (max-width: 768px) {
  .gallery-container {
    background-color: unset;
    padding: 0px;
    flex-direction: column;
    gap: 20px;
    margin: 25px 0;
  }
  .gallery-item img {
    width: 100%;
    aspect-ratio: 2/1.5;
  }
  .gallery-item h3 {
    top: calc(85% - 20px);
  }
}

